import { lazy } from "react";

import IntroContent from "../../content/IntroContent.json";
import LandingContent from "../../content/LandingContent.json";
import LandingHeader from "../../components/LandingHeader";
import { Link } from 'react-router-dom';
import about from '../About/About'
import Button from '@material-ui/core/Button';
import './homeStyles.css';

import {  Route } from "react-router-dom";
import FullWidthGrid from "../../components/LightBlub/lightBlub";
// import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Fab from '@material-ui/core/Fab';
// import AddIcon from '@material-ui/icons/Add';
// import Icon from '@material-ui/core/Icon';
// import DeleteIcon from '@material-ui/icons/Delete';
import NavigationIcon from '@material-ui/icons/Navigation';
const ContentBlock = lazy(() => import("../../components/ContentBlock"));
const Container = lazy(() => import("../../common/Container"));

const styles = theme => ({
  fab: {
    margin: theme.spacing.unit,
  },
  extendedIcon: {
    marginRight: theme.spacing.unit,
  },
});
const Home = (props) => {
  const { classes } = props;
  return (
    <Container>
      <LandingHeader/>
      <Route
        exact path="/"
        component={FullWidthGrid}
        title={IntroContent.title}
        content={IntroContent.text}
        button={IntroContent.button}
      />
     <div>
      {/* <Fab color="primary" aria-label="Add" className={classes.fab}>
        <AddIcon />
      </Fab>
      <Fab color="secondary" aria-label="Edit" className={classes.fab}>
        <Icon>edit_icon</Icon>
      </Fab> */}
       <div className="Button">
      <Fab variant="extended" aria-label="Delete" className={classes.fab}>
        <NavigationIcon className={classes.extendedIcon} />
       <Link to="/about">
      <Button onClick={about} >
          Enter JPD
        </Button>
        </Link>
          </Fab>
        
        </div>
      {/* <Fab disabled aria-label="Delete" className={classes.fab}>
        <DeleteIcon />
      </Fab> */}
    </div>
      {/* <div className="Button">
   <Link to="/about">
      <Button variant="contained" color="primary" onClick={about} >
          Enter JPD
        </Button>
        </Link>
        </div> */}
      
      <ContentBlock
        type="right"
        first="true"
        title={LandingContent.title}
        content={LandingContent.text}
        icon="developer.svg"
        id="intro"
      />

      {/* <MiddleBlock
        title={MiddleBlockContent.title}
        content={MiddleBlockContent.text}
        button={MiddleBlockContent.button}
      />
      <ContentBlock
        type="left"
        title={AboutContent.title}
        content={AboutContent.text}
        section={AboutContent.section}
        icon="graphs.svg"
        id="about"
      />
      <ContentBlock
        type="right"
        title={MissionContent.title}
        content={MissionContent.text}
        icon="product-launch.svg"
        id="mission"
      />

      <ContentBlock
        type="left"
        title={ProductContent.title}
        content={ProductContent.text}
        icon="waving.svg"
        id="product"
      />
      <ContactFrom
        title={ContactContent.title}
        content={ContactContent.text}
        id="contact"
      /> */}
    </Container>
  );
};

export default  withStyles(styles)(Home);
